// 
import VueRouter from 'vue-router'
//
import huoqu from '../pages/huoqu'
import chabiao from '../pages/chabiao'
// import Home from '../components/Home'
import index from '../pages/index'
import shopping from '../pages/shopping'
import tijiao from '../pages/tijiao'

import userinfo from '../pages/userinfo'
import editgoods from '../pages/editgoods'
import addgoods from '../pages/addgoods'
import login from '../components/login'
import uslogin from '../components/uslogin'
import register from '../pages/register'
import detail from '../pages/detail'
import usregister from '../pages/usregister'
import list from '../components/list'
import oddetail from '../pages/oddetail'
import goods from '../components/goods'
import text from '../components/text'
import order from '../pages/order'
import editorder from '../pages/editorder'

//
const router = new VueRouter({
	routes: [
		{
			path: '/index',
			component: index,
			meta: { isAuth: true, title: '' },
			children: [
				{
					path: 'huoqu',
					component: huoqu,
					meta: { isAuth: true, title: '' },
				}, {
					path: 'chabiao',
					component: chabiao,
					meta: { isAuth: true, title: '' },
				}, {
					path: 'add',
					component: addgoods,
					meta: { isAuth: true, title: '' },
				}, {
					path: 'edit',
					component: editgoods,
					meta: { isAuth: true, title: '' },
				},{
					path: 'editorder',
					component: editorder,
					meta: { isAuth: true, title: '' },
				},
			]
		},
		{
			path: '/login',
			component: login,
			meta: { title: '' },
		},
		{
			path: '/text',
			component: text,
			meta: { title: '' },
		},
		{
			path: '/register',
			component: register,
			meta: { title: '' },
		},
		{
			path: '/uslogin',
			component: uslogin,

			meta: { title: '' },

		},
		{
			path: '/usregister',
			component: usregister,
			meta: { title: '' },
		},
		{
			path: '/',
			component: list,
			meta: { title: '-', Auth: true },
			children: [
				{
					path: '',
					component: goods,
					meta: { title: '' },
				},{
					path: 'xiangqing',
					component: oddetail,
					meta: { title: '', Auth: true }
				}, {
					path: 'shopping',
					component: shopping,
					meta: { title: '' }
				}, {
					path: 'detail',
					component: detail,
					meta: { title: '' }
				}, {
					path: 'tijiao',
					component: tijiao,
					meta: { title: '', Auth: true }
				}, {
					path: 'order',
					component: order,
					meta: { title: '', Auth: true }
				}, {
					path: '/userinfo',
					component: userinfo,
					meta: { title: '', Auth: true }
				}
			]
		}
	]
})
router.beforeEach((to, from, next) => {
	if (to.meta.isAuth) {
		if (sessionStorage.getItem('user_name')) {
			next()
		} else {
			alert('')
			next('./login')

		}
	} else if (to.meta.Auth) {
		if (sessionStorage.getItem('username')) {
			next()
		} else {
			alert('')
			next('./uslogin')
		}
	} else {
		next()
	}
})
router.afterEach((to, from) => {
	document.title = to.meta.title
})
export default router